export default {
	props: {
		images: {
			type: Array,
			required: true
		}
	},
	methods: {
		getImageUrl(publicId) {
			const a = this.$img(
				publicId,
				{
					width: 600
				},
				{ provider: 'cloudinary' }
			)

			// const b = this.$cloudinary.image.url(publicId, {
			// 	width: 600,
			// 	crop: 'scale'
			// })

			return a
		}
	}
}
